import React from 'react';
import styled from 'styled-components';
import avatar from './img/PIXEL_AVATAR.png';
import SolarSystem from './_components/_components/map/SolarSystem';
import { connect } from 'react-redux';
import { TinitialState } from './store/reducer/main';

interface IApp {
  project: {name: string, description: string, link: string, index: number};
}


const MainContainer = styled.div`
  text-align: center;
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: hsl(245, 56%, 8%);
`;

const WorkWrap = styled.div`
  background-color: white;
  box-shadow: 0 0 2px black;
  padding: 1em 1.5em;
  width: calc(33vw - 1px);
`;

const ProjectStack = styled.div`
    display: flex;
    flex: flex-wrap;
    flex-direction: row;
    justify-content: center;
`;

const ProjectContainer = styled.div`
  width: 100vw;
  bottom: 0;
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: center;
`;

const ProjectCard = styled.div`
  background-color: #eee;
  padding: 1em 2em;
  text-align: center;
`;

const App: React.FC<IApp> = ({project}) => {
  const [runtime, setRuntime] = React.useState(0);

  React.useEffect(() =>  {
    const timing = setInterval(() => {
      setRuntime(runtime + 15);
    }, 30);
    return () => {
      clearInterval(timing);
    }
  } , [runtime, setRuntime])
  // const teckStack = ['Git', 'Sass', 'ReactJS', 'Redux', 'Axios', 'Bootstrap', 'Node', 'Express'];

  // const works = [['Outrun Challenge', 'https://outrun-challenge.herokuapp.com/'], 
  // ['Food Delivery template with React Native Web', 'https://native-food-delivery.herokuapp.com/'],
  // ['Underwater Marketplace Template', 'https://marketplace-template.herokuapp.com/'],
  // ['Explore Gallery', 'https://sample-portfolio-brainclouder.herokuapp.com/explore']];


  return <MainContainer>
    <SolarSystem scale={20} navigation={[0, 0]} time={runtime} />

    {
      project.index !== -1 ? 
      <ProjectContainer>
        <ProjectCard>
        <p>{project.name}</p>
        <p>{project.description}</p>
        </ProjectCard>
      </ProjectContainer>
      : ''
    }
  </MainContainer>
}

const mapStateToProps = (state: TinitialState) => {
  const t = state;
  return {
    project: t.selectedInfo
  }
}

export default connect(mapStateToProps)(App);
