import React, { Dispatch } from 'react';
import styled from 'styled-components';
import { ACTIONS, TinitialState } from '../../../store/reducer/main';
import { connect } from 'react-redux';

interface ICosmoObject {
    navigation: number[];
    distance: number;
    time: number;
    center: number[];
    selectedInfo: {index: number};
    type: string;
    size: number;
    info: any;
    rot: number;
    selectProject: (e: {name: string, description: string, link: string, index: number}) => void;
    clearProject: () => void;
}
const BottomDashed = styled.div`
    border-bottom: 4px solid  hsl(700, 99%, 45%);
    border-radius: 50%;
    position: absolute;
    z-index: 7;
`;
const TopDashed = styled.div`
    border-radius: 50%;    
    background-color: #ff000011;
    position: absolute;
    z-index: 3;
`;

const SatObj = styled.div`
    position: absolute;
    width: 5px;
    height: 5px;
    border-radius: 50%;
`;

const CosmoObject: React.FC<ICosmoObject> = ({distance, navigation, time, center, size, info, rot, selectProject, clearProject, selectedInfo}) => {
    // const [selected, selectPlanet] = React.useState(false);
    const selected = selectedInfo.index === info.index;
    const [colorSeed] = React.useState(Math.floor(Math.random() * 150) + (distance * 2));
    React.useEffect(() => {
        if (!!selected) {
            const clickHandler = (event: any) => {
                if(event.target.className !== 'referencer') {
                    clearProject();
                };
            }
            window.addEventListener('click', clickHandler);
            return () =>  window.removeEventListener('click', clickHandler);
        }   
    });
    



    const PeriodicBodyVariation = (type: string, d: number) => {
        return type === 'x' ?  Math.cos((time)/Math.pow(d/2, 2)) : Math.sin(time/Math.pow(d/2, 2));
    }


    const periodicY = PeriodicBodyVariation(`y`, distance);
    const periodicX = PeriodicBodyVariation('x', distance);
    return <>
     {info.index === 0 ? <BottomDashed style={{width: `calc(${(distance*2) - 4}px)`, 
    height: `calc(${(distance*2) - 4}px)`, 
    left: `calc(${center[0]}vw - ${distance}px)`, 
    top: `calc(${center[1]}vh - ${distance - 3}px)`}}/>  : ''}
    <TopDashed style={{width: `calc(${(distance*2) - 4}px)`, 
    height: `calc(${(distance*2) - 4}px)`, 
    left: `calc(${center[0]}vw - ${distance}px)`, 
    top: `calc(${center[1]}vh - ${distance}px)`,
    opacity: 0.6,
    borderBottom: info.index !== 0 ? '4px solid  hsl(700, 99%, 45%)': ''}}/>
    

    <div onClick={() => {

        selectProject({name: info.name, description: info.description, link: info.link, index: info.index});
    }} style={{
        width: `${selected ? size * 2 : size * 1}px`, height: `${selected ? size * 2 : size * 1}px`, 
        zIndex: selected ? 10 : periodicY < 0 ? 5 : 8, cursor: 'pointer',
        backgroundImage: `radial-gradient(circle at ${50 - (periodicX * 50)}% ${50 - (periodicY * 50)}%, 
        hsl(${colorSeed}, 73%, 73%) 0%,
        hsl(${colorSeed}, 73%, 63%) 10%, 
        hsl(${colorSeed}, 73%, 3%))`,
        transform: `rotateY(-${rot}deg) scale(${1 + (periodicY/4)})`,
        border: `0px solid ${selected ? 'hsl(269, 73%, 93%)' : 'hsl(269, 73%, 53%)'}`, borderRadius: '50%',
        position: 'absolute', 
        top: `calc(${50}vh - ${size * (selected ? 1 : 0.5)}px - ${navigation[1]/2}vh + ${periodicY * distance}px)`, 
        left: `calc(${50}vw - ${size * (selected ? 1 : 0.5)}px + ${navigation[0]/2}vw + ${periodicX * distance}px)`
    }}>          
            {
            info.satellite.length > 0 ? info.satellite.map((e: number[]) => {
                const pY = PeriodicBodyVariation('y', e[0] * 1.5);
                const pX = PeriodicBodyVariation('x', e[0] * 1.5);
            return <SatObj style={{
                top: `calc(50% - 2.5px + ${pY * e[0] * 2.5}%)`, left: `calc(50% - 2.5px + ${pX * e[0] * 2.5}%)`,
                backgroundColor: 'hsl(269, 73%, 63%)'

            }} />}) : ''
            }

{
     selected ? 
     <div className={'referencer'} style={{
        justifyContent: 'center', 
        position: 'absolute', 
        // top: periodicY > 0 ? '-160px' : '', bottom: periodicY < 0 ? '-160px' : '',
        marginLeft: '-60px'}}>
            {/* <div>
            <a href={info.link}>{info.name}</a>
            </div> */}
            {/* <a href={info.link}>
                    {info.description}
                    </a> */}
            <div>
            </div>
            <div style={{
                borderTop: '25px solid #eee',
                borderLeft: '25px solid transparent',
                borderRight: '25px solid transparent',
                position: 'absolute',
                left: '55px',
                bottom: periodicY < 0 ? '' : '-25px',
                top: '-25px'
            }} />
            </div> : ''
            }
    </div>   
    </>
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        selectProject: (e: {name: string, description: string, link: string, index: number}) => dispatch({type: ACTIONS.selectProject, payload: e}),
        clearProject: () => dispatch({type: ACTIONS.clearProject})
    }
}

const mapStateToProps = (state: TinitialState) => {
    const t = state;
    return {
        selectedInfo: t.selectedInfo,
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(CosmoObject);